<template>
    <div class="d-inline">

        <b-button class="btn btn-primary btn-round btn-lg" v-b-modal.modal-prevent-closing>  </b-button>

        <b-modal
        id="modal-prevent-closing"
        ref="modal"
        title="Profile"
        hide-footer
        >
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
                label="NICKNAME"
                label-for="name-input"
            >
            <b-form-input
                id="name-input"
                v-model="userData.name"
                required
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="PHONE"
                label-for="phone-input"
            >
            <b-form-input
                type="text"
                id="phone-input"
                v-model="userData.phone"
                required
            ></b-form-input>
            </b-form-group>

            <b-form-group id="input-group-3" label="1" label-for="inter1-input">
              <b-form-select
                id="inter1-input"
                v-model="userData.interest1"
                :options="interests"
                required
              ></b-form-select>
            </b-form-group>

            <b-form-group id="input-group-3" label="2" label-for="inter1-input" description=" 1  ">
              <b-form-select
                id="inter1-input"
                v-model="userData.interest2"
                :options="interests"
                required
              ></b-form-select>
            </b-form-group>


            <b-button class="mt-3" block @click=handleSubmit>Update</b-button>
        </form>
        </b-modal>
    </div>

</template>

<script>
import axios from 'axios'
import { sync } from 'vuex-pathify'

const API_URL = 'http://localhost:8080/'

export default {
    name: 'ProfileUpdate',

    data() {
      return {
        interests: [
          { text: '   ', value: '' },
          '',
          '',
          'IT',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
        ],
      }
    },
    
    props: ['userData'],

    computed: {
        curUid: sync('curUid')
    },

    methods: {
        handleSubmit() {
            // Exit when the form isn't valid
            if (this.userData.interest1 === this.userData.interest2) {
                alert('   ')
                return
            }

            var putData = {
                email: this.userData.email,
                uid: this.userData.uid,
                password: this.userData.password,
                name: this.userData.name,
                phone: this.userData.phone,
                interest1: this.userData.interest1,
                interest2: this.userData.interest2,
                description: this.userData.description,
                img: this.userData.img,    
            }

            console.log(putData)
            
            const url = `${API_URL}account/update`

            axios.put(url, putData)
            .then(res => {
                this.$nextTick(() => {
                this.$bvModal.hide('modal-prevent-closing')
                })
                console.log('profile_update_success')
                location.reload()
                alert('  ')

            })
            .catch(() => {
                alert('   .')
                return
            })
            
        },
    }
}
</script>

