<template>
    <div class="d-inline">

        <b-button class="btn btn-primary btn-round btn-lg" v-b-modal.des-update-modal>  </b-button>

        <b-modal
        id="des-update-modal"
        ref="modal"
        title="Description"
        hide-footer
        >
        <form ref="form" @submit.stop.prevent="handleSubmit">
            <b-form-group
                label=""
                label-for="name-input"
                description="500  "
            >
            <b-form-textarea
              id="textarea-state"
              v-model="userData.description"
              :state="userData.description.length <= 500"
              placeholder="  "
              rows="3"
            ></b-form-textarea>
            </b-form-group>

            <b-button class="mt-3" block @click=handleSubmit>Update</b-button>
        </form>
        </b-modal>
    </div>

</template>

<script>
import axios from 'axios'
import { sync } from 'vuex-pathify'

const API_URL = 'http://localhost:8080/'

export default {
    name: 'ProfileUpdate',

    data() {
      return {
        interests: [
          { text: '   ', value: '' },
          '',
          '',
          'IT',
          '',
          '',
          '',
          '',
          '',
          '',
          '',
        ],
      }
    },
    
    props: ['userData'],

    computed: {
        curUid: sync('curUid')
    },

    methods: {
        handleSubmit() {

            if (this.userData.description.length > 500) {
                alert('500  ')
                return
            }

            var putData = {
                uid: this.curUid,
                description: this.userData.description,
            }
            
            const url = `${API_URL}account/update/description`

            axios.put(url, putData)
            .then(res => {
                this.$nextTick(() => {
                this.$bvModal.hide('des-update-modal')
                })

                console.log('description_update_success')
                location.reload()
                alert('  ')
            })
            .catch(() => {
                alert('   .')
                return
            })
            
        },
    },
}
</script>

