<template>
    <div>
        <b-button class="btn-primary" v-if="!curUid" @click="loginAlert"> </b-button>
        <b-button v-b-modal.modal-multi-1 class="btn-primary" @click="openModal"> </b-button>

        <b-modal id="modal-multi-1" size="lg" title="  " hide-footer no-stacking>

            <form ref="form" @submit.stop.prevent="handleSubmit">

            <b-form-group
                label=""
                label-for="tag-input"
            >
            <b-form-input
                id="tag-input"
                v-model="hashTag.tagname"
                required
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label=""
                label-for="company-input"
            >
            <b-form-input
                id="company-input"
                v-model="introduction.company"
                required
            ></b-form-input>
            </b-form-group>


            <b-form-group
                label=" "
                label-for="startdate-input"
            >
            <b-form-input
                type="text"
                id="startdate-input"
                v-model="introduction.startdate"
                placeholder="-- ex> 2020-8-11"
                required
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label=" "
                label-for="enddate-input"
            >
            <b-form-input
                type="text"
                id="enddate-input"
                v-model="introduction.enddate"
                placeholder="-- ex> 2020-8-11"
                required
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label=" "
                label-for="subjectno-input"
            >
            <b-form-input
                id="subjectno-input"
                v-model="introduction.subjectno"
                required
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label="  "
                label-for="limit-input"
            >
            <b-form-input
                id="limit-input"
                v-model="introduction.limitlength"
                required
            ></b-form-input>
            </b-form-group>

            </form>
            
            <div class="d-flex flex-row-reverse">
                <b-button class="btn-round btn-info" @click="nextModal"></b-button>
            </div>
        </b-modal>

        <b-modal id="modal-multi-2" size="lg" title=" " hide-footer no-stacking>

            <b-form-group
                label=""
                label-for="subject-input"
            >
            <b-form-input
                id="subject-input"
                v-model="introduction.subject"
                required
            ></b-form-input>
            </b-form-group>

            <b-form-group
                label=""
                label-for="name-input"
            >
            <b-form-textarea
            id="textarea-state"
            placeholder=" "
            rows="12"
            v-model="introduction.contents"
            ></b-form-textarea>
            </b-form-group>

            <b-button class="btn-round btn-info" @click="prevModal"></b-button>

            <div class="d-flex flex-row-reverse">
                <b-button clas="d-flex flex-row-reverse btn-primary" size="md" @click=handleSubmit></b-button>
            </div>
        </b-modal>
    </div>
</template>

<script>
import axios from 'axios'
import {sync} from 'vuex-pathify'

const API_URL = 'http://localhost:8080/'

export default {
    name: 'IntroCreate',

    computed: {
        curUid: sync('curUid')
    },

    data() {
        return {
            hashTag: {
                tagname: ''
            },

            introduction: {
                uid: '',
                company: '',
                startdate: '',
                enddate: '',
                subjectno: '',
                subject: '',
                contents: '',
                limitlength: '',
            }
        }
    },
    methods: {
        openModal() {
            this.resetModal()
            this.$bvModal.show('modal-multi-1')
        },

        resetModal() {
            this.hashTag.tagname = ''
            this.introduction.uid = this.curUid
            this.introduction.company = ''
            this.introduction.startdate = ''
            this.introduction.enddate = ''
            this.introduction.subjectno = ''
            this.introduction.subject = ''
            this.introduction.contents = ''
            this.introduction.limitlength = ''
            this.introduction.subject = ''
            this.introduction.contents = ''

            return
        },

        nextModal() {
            if (this.hashTag.tagname === '') {
                alert(' ')
                return
            }
            if (this.introduction.company === '') {
                alert(' ')
                return
            }
            if (this.introduction.startdate === '') {
                alert(' ')
                return
            }
            if (this.introduction.enddate === '') {
                alert(' ')
                return
            }
            if (this.introduction.subjectno === '') {
                alert('  ')
                return
            }
            if (this.introduction.limitlength === '') {
                alert('   ')
                return
            }

            this.$bvModal.hide('modal-multi-1')
            this.$bvModal.show('modal-multi-2')
        },
        
        prevModal() {
            this.$bvModal.hide('modal-multi-2')
            this.$bvModal.show('modal-multi-1')
        },

        handleSubmit() {
            if (this.introduction.subject === '') {
                alert('  ')
                return
            }
            if (this.introduction.contents === '') {
                alert('  ')
                return
            }

            const url = `${API_URL}introduction/create`
            var postData = {
                introduction: this.introduction,
                hashTag: this.hashTag,
            }

            axios.post(url, postData)
            .then(res => {
                console.log('')
                console.log(res)
                this.$bvModal.hide('modal-multi-2')
                location.reload()
            })
            .catch(() => {
                alert('  ')
                return
            })
        },

        createBoard(){
            this.boardData.uid = this.curUid
            axios.post(`${API_URL}api/board/create/`, this.boardData)
            .then(res=>{
                alert('  ')
                location.reload()
            })
            .catch(err=> {
                console.log(err)
            })
        },

        loginAlert() {
            alert(' ')
        }
    },

    mounted() {
        this.introduction.uid = this.curUid
        
    },
    

}
</script>

<style>

</style>